// @spec: specs/003-modern-ui-ux/plan.md
// Custom animation hooks

import { useReducedMotion } from "framer-motion";
import { Variants, Transition } from "framer-motion";
import { staggerDelay } from "./easing";

// Hook for animation variants with reduced motion support
export function useAnimationVariant<T extends Variants>(
  variants: T,
  customTransition?: Transition
) {
  const shouldReduceMotion = useReducedMotion();

  return {
    variants: shouldReduceMotion
      ? {
          hidden: { opacity: 1 },
          visible: { opacity: 1 },
        }
      : variants,
    transition: shouldReduceMotion
      ? { duration: 0 }
      : customTransition || { duration: 0.4 },
  };
}

// Hook for stagger animations
export function useStagger(
  staggerChildren: number = staggerDelay.normal,
  delayChildren: number = 0.1
) {
  const shouldReduceMotion = useReducedMotion();

  return {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: shouldReduceMotion ? 0 : staggerChildren,
        delayChildren: shouldReduceMotion ? 0 : delayChildren,
      },
    },
  };
}

// Hook for page transitions
export function usePageTransition() {
  const shouldReduceMotion = useReducedMotion();

  return {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: {
      duration: shouldReduceMotion ? 0 : 0.4,
      ease: "easeOut",
    },
  };
}

// Hook for hover scale effect
export function useHoverScale(scale: number = 1.02) {
  return {
    whileHover: { scale },
    whileTap: { scale: 0.98 },
    transition: { type: "spring", stiffness: 400, damping: 17 },
  };
}

// Hook for button interactions
export function useButtonAnimation() {
  const shouldReduceMotion = useReducedMotion();

  return {
    variants: {
      idle: { scale: 1 },
      hover: { scale: shouldReduceMotion ? 1 : 1.02 },
      tap: { scale: shouldReduceMotion ? 1 : 0.98 },
    },
    transition: { duration: 0.2 },
  };
}

// Hook for focus ring animation
export function useFocusAnimation() {
  const shouldReduceMotion = useReducedMotion();

  return {
    whileFocus: {
      scale: shouldReduceMotion ? 1 : 1.02,
      boxShadow: "0 0 0 3px rgba(14, 165, 233, 0.2)",
    },
    transition: { duration: 0.2 },
  };
}

// Hook for dialog animations
export function useDialogAnimation() {
  const shouldReduceMotion = useReducedMotion();

  return {
    overlay: {
      hidden: { opacity: 0 },
      visible: { opacity: 1, transition: { duration: shouldReduceMotion ? 0 : 0.2 } },
      exit: { opacity: 0, transition: { duration: shouldReduceMotion ? 0 : 0.15 } },
    },
    dialog: {
      hidden: { opacity: 0, scale: 0.95, y: 20 },
      visible: {
        opacity: 1,
        scale: 1,
        y: 0,
        transition: {
          type: "spring",
          duration: shouldReduceMotion ? 0 : 0.4,
          bounce: 0.3,
        },
      },
      exit: { opacity: 0, scale: 0.95, y: 20, transition: { duration: shouldReduceMotion ? 0 : 0.2 } },
    },
  };
}

// Hook for list animations (reorder, drag)
export function useListAnimation() {
  const shouldReduceMotion = useReducedMotion();

  return {
    layout: true,
    initial: { opacity: 0, x: -20 },
    animate: { opacity: 1, x: 0 },
    exit: { opacity: 0, x: 20, height: 0 },
    transition: {
      duration: shouldReduceMotion ? 0 : 0.3,
      type: "spring",
      stiffness: 100,
      damping: 20,
    },
  };
}
