// @spec: specs/003-modern-ui-ux/plan.md
// GSAP animation hooks for React

"use client";

import { useRef, useEffect, RefObject } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// Type for GSAP context
type GsapContext = gsap.Context;

// Hook for simple GSAP animations with auto-cleanup
export function useGsapAnimation<T extends HTMLElement = HTMLElement>(
  callback: (element: T, ctx: GsapContext) => void | (() => void),
  dependencies: unknown[] = []
): RefObject<T> {
  const elementRef = useRef<T>(null);

  useEffect(() => {
    const element = elementRef.current;
    if (!element) return;

    const ctx = gsap.context(() => {
      callback(element, ctx);
    }, element);

    return () => ctx.revert();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, dependencies);

  return elementRef;
}

// Hook for scroll-triggered animations
export function useScrollReveal<T extends HTMLElement = HTMLElement>(
  options: {
    y?: number;
    opacity?: number;
    duration?: number;
    delay?: number;
    start?: string;
    toggleActions?: string;
  } = {}
): RefObject<T> {
  const elementRef = useRef<T>(null);

  useEffect(() => {
    const element = elementRef.current;
    if (!element) return;

    const ctx = gsap.context(() => {
      gsap.fromTo(
        element,
        {
          y: options.y ?? 50,
          opacity: options.opacity ?? 0,
        },
        {
          y: 0,
          opacity: 1,
          duration: options.duration ?? 0.8,
          delay: options.delay ?? 0,
          ease: "power3.out",
          scrollTrigger: {
            trigger: element,
            start: options.start ?? "top 85%",
            toggleActions: options.toggleActions ?? "play none none reverse",
          },
        }
      );
    }, element);

    return () => ctx.revert();
  }, [options.y, options.opacity, options.duration, options.delay, options.start, options.toggleActions]);

  return elementRef;
}

// Hook for parallax effects
export function useParallax<T extends HTMLElement = HTMLElement>(
  speed: number = 0.5,
  start: string = "top bottom",
  end: string = "bottom top"
): RefObject<T> {
  const elementRef = useRef<T>(null);

  useEffect(() => {
    const element = elementRef.current;
    if (!element) return;

    const ctx = gsap.context(() => {
      gsap.to(element, {
        y: -50 * speed,
        ease: "none",
        scrollTrigger: {
          trigger: element,
          start,
          end,
          scrub: true,
        },
      });
    }, element);

    return () => ctx.revert();
  }, [speed, start, end]);

  return elementRef;
}

// Hook for pin-and-scrub animations
export function usePinScrub<T extends HTMLElement = HTMLElement>({
  start = "top top",
  end = "+=100%",
  scrub = 1,
}: {
  start?: string;
  end?: string;
  scrub?: boolean | number;
} = {}): RefObject<T> {
  const elementRef = useRef<T>(null);

  useEffect(() => {
    const element = elementRef.current;
    if (!element) return;

    const ctx = gsap.context(() => {
      ScrollTrigger.create({
        trigger: element,
        start,
        end,
        scrub,
        pin: true,
        anticipatePin: 1,
      });
    }, element);

    return () => ctx.revert();
  }, [start, end, scrub]);

  return elementRef;
}

// Hook for horizontal scroll section
export function useHorizontalScroll<T extends HTMLElement = HTMLElement>({
  sections = 3,
  gap = 0,
}: {
  sections?: number;
  gap?: number;
} = {}): RefObject<T> {
  const containerRef = useRef<T>(null);

  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const ctx = gsap.context(() => {
      const sectionElements = gsap.utils.toArray(".horizontal-section");
      if (sectionElements.length === 0) return;

      gsap.to(sectionElements, {
        xPercent: -100 * (sectionElements.length - 1),
        ease: "none",
        scrollTrigger: {
          trigger: container,
          pin: true,
          scrub: 1,
          snap: 1 / (sectionElements.length - 1),
          end: () => "+=" + container.offsetWidth,
        },
      });
    }, container);

    return () => ctx.revert();
  }, [sections, gap]);

  return containerRef;
}

// Hook for timeline animations
export function useTimeline<T extends HTMLElement = HTMLElement>(
  callback: (timeline: gsap.core.Timeline) => void,
  dependencies: unknown[] = []
): RefObject<T> {
  const elementRef = useRef<T>(null);

  useEffect(() => {
    const element = elementRef.current;
    if (!element) return;

    const ctx = gsap.context(() => {
      const timeline = gsap.timeline();
      callback(timeline);
    }, element);

    return () => ctx.revert();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, dependencies);

  return elementRef;
}

// Hook for stagger animations on lists
export function useStaggerList<T extends HTMLElement = HTMLElement>(
  selector: string,
  staggerAmount: number = 0.08,
  startAnimation: string = "top 80%"
): RefObject<T> {
  const containerRef = useRef<T>(null);

  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const ctx = gsap.context(() => {
      const items = container.querySelectorAll(selector);
      if (items.length === 0) return;

      gsap.fromTo(
        items,
        { opacity: 0, y: 20 },
        {
          opacity: 1,
          y: 0,
          duration: 0.5,
          stagger: staggerAmount,
          ease: "power3.out",
          scrollTrigger: {
            trigger: container,
            start: startAnimation,
          },
        }
      );
    }, container);

    return () => ctx.revert();
  }, [selector, staggerAmount, startAnimation]);

  return containerRef;
}
