// @spec: specs/003-modern-ui-ux/plan.md
// Reduced motion support utilities

import { useReducedMotion } from "framer-motion";
import { Transition } from "framer-motion";

// Get transition that respects reduced motion preference
export function getReducedMotionTransition(
  normalDuration: number = 0.4
): Transition {
  const shouldReduceMotion = useReducedMotion();
  return {
    duration: shouldReduceMotion ? 0 : normalDuration,
  };
}

// Get initial state that respects reduced motion
export function getReducedMotionInitial(
  animatedInitial: object = { opacity: 0, y: 20 },
  reducedInitial: object = { opacity: 1 }
): object {
  const shouldReduceMotion = useReducedMotion();
  return shouldReduceMotion ? reducedInitial : animatedInitial;
}

// Get animate state that respects reduced motion
export function getReducedMotionAnimate(
  animatedAnimate: object = { opacity: 1, y: 0 },
  reducedAnimate: object = { opacity: 1 }
): object {
  const shouldReduceMotion = useReducedMotion();
  return shouldReduceMotion ? reducedAnimate : animatedAnimate;
}

// Hook to get duration based on reduced motion
export function useAnimationDuration(defaultDuration: number = 0.4): number {
  const shouldReduceMotion = useReducedMotion();
  return shouldReduceMotion ? 0 : defaultDuration;
}

// Hook to get stagger delay based on reduced motion
export function useStaggerDelay(defaultDelay: number = 0.08): number {
  const shouldReduceMotion = useReducedMotion();
  return shouldReduceMotion ? 0 : defaultDelay;
}

// Animation config that disables animations for reduced motion
export const reducedMotionConfig = {
  transition: { duration: 0 },
  ease: "linear" as const,
};

// Create animation variants that respect reduced motion
export function createReducedMotionVariant(
  animatedVariant: object,
  reducedVariant: object = { opacity: 1 }
) {
  const shouldReduceMotion = useReducedMotion();
  return shouldReduceMotion ? reducedVariant : animatedVariant;
}
