// @spec: specs/003-modern-ui-ux/spec.md
// @spec: specs/003-modern-ui-ux/plan.md
// GSAP context provider for React

"use client";

import React, { createContext, useContext, useEffect, useRef, ReactNode } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// Register ScrollTrigger plugin
if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger);
}

interface GsapContextValue {
  gsap: typeof gsap;
  ScrollTrigger: typeof ScrollTrigger;
  ctx: gsap.Context | null;
}

const GsapContext = createContext<GsapContextValue | null>(null);

interface GsapProviderProps {
  children: ReactNode;
}

export function GsapProvider({ children }: GsapProviderProps) {
  const ctxRef = useRef<gsap.Context | null>(null);

  useEffect(() => {
    // Create a GSAP context for proper cleanup
    ctxRef.current = gsap.context(() => {
      // Global GSAP setup can go here
    });

    return () => {
      // Cleanup on unmount
      ctxRef.current?.revert();
    };
  }, []);

  const value: GsapContextValue = {
    gsap,
    ScrollTrigger,
    ctx: ctxRef.current,
  };

  return <GsapContext.Provider value={value}>{children}</GsapContext.Provider>;
}

export function useGsap() {
  const context = useContext(GsapContext);
  if (!context) {
    throw new Error("useGsap must be used within a GsapProvider");
  }
  return context;
}

// Hook for using GSAP with React refs (auto-cleanup)
export function useGsapAnimation(
  callback: (ctx: gsap.Context) => void,
  dependencies: unknown[] = []
) {
  const { gsap } = useGsap();
  const ref = useRef<HTMLElement | null>(null);

  useEffect(() => {
    if (!ref.current) return;

    const ctx = gsap.context(() => {
      callback(ctx);
    }, ref);

    return () => ctx.revert();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, dependencies);

  return ref;
}

// Hook for ScrollTrigger animations
export function useScrollTrigger(
  options: {
    trigger?: HTMLElement | null;
    start?: string;
    end?: string;
    scrub?: boolean | number;
    toggleActions?: string;
  } = {}
) {
  const { ScrollTrigger } = useGsap();
  const triggerRef = useRef<HTMLElement | null>(null);

  useEffect(() => {
    if (!triggerRef.current) return;

    const trigger = ScrollTrigger.create({
      trigger: triggerRef.current,
      start: options.start || "top 80%",
      end: options.end || "bottom 20%",
      scrub: options.scrub ?? false,
      toggleActions: options.toggleActions || "play none none reverse",
    });

    return () => {
      trigger.kill();
    };
  }, [ScrollTrigger, options.start, options.end, options.scrub, options.toggleActions]);

  return triggerRef;
}
