// Animation easing presets

import { CubicBezierDefinition } from "framer-motion";

// Standard easings
export const easing: Record<string, CubicBezierDefinition | string> = {
  // CSS-like easings
  easeIn: [0.4, 0, 1, 1] as CubicBezierDefinition,
  easeOut: [0, 0, 0.2, 1] as CubicBezierDefinition,
  easeInOut: [0.4, 0, 0.2, 1] as CubicBezierDefinition,

  // Spring-like
  spring: [0.175, 0.885, 0.32, 1.275] as CubicBezierDefinition,
  springBounce: [0.68, -0.55, 0.265, 1.55] as CubicBezierDefinition,

  // Smooth transitions
  smooth: "cubic-bezier(0.4, 0, 0.2, 1)" as string,
  smoothOut: "cubic-bezier(0.0, 0.0, 0.2, 1)" as string,

  // Linear
  linear: "linear" as string,
};

// Transition defaults
export const transition = {
  fast: { duration: 0.15 },
  normal: { duration: 0.3 },
  slow: { duration: 0.5 },
  page: { duration: 0.4 },
};

// Spring configs
export const springTransition = {
  type: "spring",
  stiffness: 100,
  damping: 20,
};

export const gentleSpring = {
  type: "spring",
  stiffness: 85,
  damping: 15,
};

export const bouncySpring = {
  type: "spring",
  stiffness: 400,
  damping: 10,
};

// Stagger delays
export const staggerDelay = {
  fast: 0.05,
  normal: 0.08,
  slow: 0.12,
};
