// Component-specific animation presets

import { Variants } from "framer-motion";
import { buttonVariants } from "./variants";
import { dialogVariants, overlayVariants } from "./variants";
import { cardVariants } from "./variants";
import { inputVariants } from "./variants";

// Button preset - already exported from variants
export { buttonVariants };

// Card preset with hover lift
export const animatedCardPresets: Variants = {
  ...cardVariants,
  hover: {
    y: -4,
    boxShadow:
      "0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1)",
    transition: { duration: 0.2 },
  },
};

// Dialog preset
export const dialogPresets = {
  dialog: dialogVariants,
  overlay: overlayVariants,
};

// Input preset
export const inputPresets = {
  variants: inputVariants,
  transition: { duration: 0.2 },
};

// Task item preset for task lists
export const taskItemVariants: Variants = {
  hidden: { opacity: 0, x: -20 },
  visible: { opacity: 1, x: 0, transition: { duration: 0.3 } },
  hover: { scale: 1.01, transition: { duration: 0.15 } },
  tap: { scale: 0.99, transition: { duration: 0.1 } },
  exit: {
    opacity: 0,
    x: 20,
    height: 0,
    transition: { duration: 0.2 },
  },
};

// Tag chip preset
export const tagChipVariants: Variants = {
  hidden: { opacity: 0, scale: 0.8 },
  visible: { opacity: 1, scale: 1, transition: { duration: 0.2 } },
  hover: { scale: 1.05 },
};

// Loading skeleton preset
export const skeletonVariants: Variants = {
  initial: { opacity: 0.5 },
  animate: {
    opacity: [0.5, 1, 0.5],
    transition: {
      duration: 1.5,
      repeat: Infinity,
      ease: "easeInOut",
    },
  },
};

// Toast notification preset
export const toastVariants: Variants = {
  hidden: { opacity: 0, x: 100, scale: 0.95 },
  visible: {
    opacity: 1,
    x: 0,
    scale: 1,
    transition: { type: "spring", duration: 0.4, bounce: 0.3 },
  },
  exit: {
    opacity: 0,
    x: 100,
    scale: 0.95,
    transition: { duration: 0.2 },
  },
};

// Dropdown menu preset
export const dropdownVariants: Variants = {
  hidden: { opacity: 0, y: -10, scale: 0.95 },
  visible: {
    opacity: 1,
    y: 0,
    scale: 1,
    transition: { duration: 0.2, ease: "easeOut" },
  },
  exit: {
    opacity: 0,
    y: -10,
    scale: 0.95,
    transition: { duration: 0.15 },
  },
};

// Staggered list item preset
export const staggerItemVariants: Variants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0 },
};

// Checkbox preset
export const checkboxVariants: Variants = {
  unchecked: { scale: 1 },
  checked: {
    scale: [1, 1.2, 1],
    transition: { duration: 0.3 },
  },
};

// Priority badge preset
export const priorityBadgeVariants: Variants = {
  hidden: { opacity: 0, scale: 0.8 },
  visible: { opacity: 1, scale: 1, transition: { type: "spring", bounce: 0.4 } },
};
